export default {
  name: "index",
  data() {
    return {
      isLoading: false, // 加载中转菊花
      noMore: false, // 是否还有更多
      err:false
    }
  },
  methods: {
    loadMore() {
      this.isMoreLoading = true // 设置加载更多中
      this.isLoading = true // 加载中
      this.$emit('loadMore')
    },
  },
  computed: {
    scrollDisabled() {
      if (this.noMore) {
        return true
      } else {
        return this.isLoading
      }
    }
  },
  mounted() {
    this.$on('finishInfinite', (val) => {
      // console.log(val)
      this.isLoading = false
      if (val) this.noMore = val
    });
    this.$on('errInfinite', () => {
      this.isLoading = false
      this.noMore = true
      this.err = true
    });
  },
}



